import Page from 'components/Page';
import { Consumer } from "../components/Context";
import { DisplayWidget } from 'components/Widget';
import React from 'react';
import SourceLink from 'components/SourceLink';
import { Pie, Doughnut, Bar, Line,Polar, HorizontalBar,Scatter, } from 'react-chartjs-2';
import { getColor } from 'utils/colors';
import { randomNum } from 'utils/demos';
import conf from "../api/config";
import { findDashboardMeta } from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  Alert,
} from 'reactstrap';
const rand = () => Math.round(Math.random() * 20 - 10)

class DashboardPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visualizations : [],
      revexp:[],
      toptentrucks:[],
      driver_rev:[],
      fuel_cons:[],
      broker_loads:[],
      fuel_mile:[],
      fuel_rev:[],
      fuel_weight:[],
      has_reminders:'',
      reminders:[],
      xtoken:'',
    };
    AsyncStorage.getItem(conf.secret)
    .then((results) => {
      if( results !== null )
      {
        let obje_results = JSON.parse(results);
        this.setState({
          ...this.state,
          xtoken : obje_results.user.token,
        });
        findDashboardMeta(obje_results.user.token).then((response) => {
            this.setState({
                ...this.state,
                visualizations:response.data,
                revexp:response.data.revexp,
                toptentrucks:response.data.top_ten,
                driver_rev:response.data.driver_rev,
                fuel_cons:response.data.fuel_cons,
                broker_loads:response.data.broker_loads,
                fuel_mile:response.data.fuel_mile,
                fuel_rev:response.data.fuel_rev,
                fuel_weight:response.data.fuel_weight,
                has_reminders:response.data.has_reminders,
                reminders:response.data.reminders,
            });
            console.log('reminders data', response.data.reminders,);
        }).catch((respError) => {
          console.log('respError', respError);
        });
      }
    })
    .catch((error) => {
      console.log('storage error', error);
    });
  }
  genRevenueLineData = (input, moreData = {}, moreData2 = {}) => {
    const MONTHS = this.extractSetMonthKeys(input);
    return {
      labels: MONTHS,
      datasets: [
        {
          label: 'Revenue($)',
          backgroundColor: getColor('secondary'),
          borderColor: getColor('secondary'),
          fill:false,
          borderWidth: 1,
          data: this.extractSetRevValues(input),
          ...moreData2,
        },
      ],
    };
  };
  genTruckRevHoriBarData = (input) => {
    return { data: {
          labels: this.extractSetKeys(input),
          datasets: [
            {
              label: 'Truck revenue($)',
              backgroundColor: getColor('secondary'),
              borderColor: getColor('secondary'),
              borderWidth: 1,
              hoverBackgroundColor: getColor('secondary'),
              hoverBorderColor: getColor('secondary'),
              data: this.extractSetValues(input),
            }
          ]
        },
        options: {
          scales: {
            xAxes: [{
                gridLines: {
                    color: "rgba(0, 0, 0, 0)",
                }
            }],
            yAxes: [{
                gridLines: {
                    color: "rgba(0, 0, 0, 0)",
                }   
            }]
          }
        }
      };
  };
  genDriverRevenueBarData = (input) => {
    return {
      labels: this.extractSetKeys(input),
      datasets: [
        {
          label: 'Driver revenue',
          data: this.extractSetValues(input),
          backgroundColor: [
            'rgba(255, 99, 132, 0.2)',
            'rgba(54, 162, 235, 0.2)',
            'rgba(255, 206, 86, 0.2)',
            'rgba(75, 192, 192, 0.2)',
            'rgba(153, 102, 255, 0.2)',
            'rgba(255, 159, 64, 0.2)',
            'rgba(255, 99, 132, 0.2)',
            'rgba(54, 162, 235, 0.2)',
            'rgba(255, 206, 86, 0.2)',
            'rgba(75, 192, 192, 0.2)',
          ],
          borderColor: [
            'rgba(255, 99, 132, 1)',
            'rgba(54, 162, 235, 1)',
            'rgba(255, 206, 86, 1)',
            'rgba(75, 192, 192, 1)',
            'rgba(153, 102, 255, 1)',
            'rgba(255, 159, 64, 1)',
            'rgba(255, 99, 132, 1)',
            'rgba(54, 162, 235, 1)',
            'rgba(255, 206, 86, 1)',
            'rgba(75, 192, 192, 1)',
          ],
          borderWidth: 1,
        },
      ],
    }
  }
  genFuelConsumptionLineData = (input, moreData = {}, moreData2 = {}) => {
    const MONTHS = this.extractSetMonthKeys(input);
    return {
      labels: MONTHS,
      datasets: [
        {
          label: 'Consumption($)',
          backgroundColor: getColor('secondary'),
          borderColor: getColor('secondary'),
          fill:false,
          borderWidth: 1,
          data: this.extractSetRevValues(input),
          ...moreData2,
        },
      ],
    };
  };
  genFuelMilesScatterData = (fuelData, mileData) => {
    return {
      datasets: [
        {
          label: 'Fuel',
          data: fuelData,
          backgroundColor: getColor('secondary'),
        },
        {
          label: 'Miles',
          data: mileData,
          backgroundColor: getColor('success'),
        },
      ],
    }
  }
  genFuelRevenueScatterData = (fuelData, revenueData) => {
    return {
      datasets: [
        {
          label: 'Fuel',
          data: fuelData,
          backgroundColor: getColor('secondary'),
        },
        {
          label: 'Revenue',
          data: revenueData,
          backgroundColor: getColor('danger'),
        },
      ],
    }
  }
  genFuelWeightScatterData = (fuelData, weightData) => {
    return {
      datasets: [
        {
          label: 'Fuel',
          data: fuelData,
          backgroundColor: getColor('warning'),
        },
        {
          label: 'Weight',
          data: weightData,
          backgroundColor: getColor('primary'),
        },
      ],
    }
  }
  genBrokerLoadsCountHoriBarData = (input) => {
    return { data: {
          labels: this.extractSetKeys(input),
          datasets: [
            {
              label: 'Load count',
              backgroundColor: getColor('primary'),
              borderColor: getColor('primary'),
              borderWidth: 1,
              hoverBackgroundColor: getColor('primary'),
              hoverBorderColor: getColor('primary'),
              data: this.extractSetValues(input),
            }
          ]
        },
        options: {
          scales: {
            xAxes: [{
                gridLines: {
                    color: "rgba(0, 0, 0, 0)",
                },
                ticks: {
                  beginAtZero: true,
                },
            }],
            yAxes: [{
                gridLines: {
                    color: "rgba(0, 0, 0, 0)",
                }   
            }]
          }
        }
      };
  };
  extractSetMonthKeys = (dataset) => {
    let keys = [];
    dataset.forEach( function(x){
      let mkey = Object.keys(x)[0];
      keys.push(mkey.split('~')[1]);
    });
    return keys;
  }
  extractSetRevValues = (dataset) => {
    let keys = [];
    dataset.forEach( function(x){
      let mkey = Object.values(x)[0];
      keys.push(mkey.split('~')[0]);
    });
    return keys;
  }
  extractSetExpValues = (dataset) => {
    let keys = [];
    dataset.forEach( function(x){
      let mkey = Object.values(x)[0];
      keys.push(mkey.split('~')[1]);
    });
    return keys;
  }
  extractSetKeys = (dataset) => {
    let keys = [];
    dataset.forEach( function(x){
      keys.push(Object.keys(x)[0])
    });
    return keys;
  }
  extractSetValues = (dataset) => {
    let values = [];
    dataset.forEach( function(x){
      values.push(Object.values(x)[0])
    });
    return values;
  }
  
  genTopExpensePieData = (input) => {
    let set_lables = this.extractSetKeys(input);
    return {
      datasets: [
        {
          data: this.extractSetValues(input),
          backgroundColor: [
            getColor('primary'),
            getColor('secondary'),
            getColor('success'),
            getColor('info'),
            getColor('danger'),
          ],
          label: set_lables[0],
        },
      ],
      labels: set_lables,
    };
  };
  
  genLoadsByTruckPieData = (input) => {
    let set_lables = this.extractSetKeys(input);
    return {
      datasets: [
        {
          data: this.extractSetValues(input).slice(0, 5),
          backgroundColor: [
            getColor('primary'),
            getColor('secondary'),
            getColor('success'),
            getColor('info'),
            getColor('danger'),
          ],
          label: set_lables[0],
        },
      ],
      labels: set_lables.slice(0, 5),
    };
  };
  
  genTruckMileagePieData = (input) => {
    let set_lables = this.extractSetKeys(input);
    return {
      datasets: [
        {
          data: this.extractSetValues(input).slice(0, 5),
          backgroundColor: [
            getColor('primary'),
            getColor('secondary'),
            getColor('success'),
            getColor('info'),
            getColor('danger'),
          ],
          label: set_lables[0],
        },
      ],
      labels: set_lables.slice(0, 5),
    };
  };

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  render() {
    const primaryColor = getColor('primary');
    const secondaryColor = getColor('secondary');
    return (
      <Consumer>
        {context => (
          <Page
            className="DashboardPage"
            title={process.env.REACT_APP_NAME}
            breadcrumbs={[{ name: 'Dashboard', active: true }]}
          >
            
            <Row>
              <Col lg={12} md={12} sm={12} xs={12}>
                {parseInt(context.state.user.has_setup) === 0 && (
                  <Alert color="danger">You have not completed your <b>{process.env.REACT_APP_NAME}</b> system setup. 
                    <SourceLink target="_self" href="/co/setup">
                    {" "} Complete setup
                    </SourceLink>
                  </Alert>
                )}
              </Col>
            </Row>
            {parseInt(this.state.has_reminders) === 1 && (
            <Row>
              <Col lg={12} md={12} sm={12} xs={12}>
                  <Alert color="danger">
                    <b>System Notifications</b> <br></br>
                    <p>The following trucks have got expired documents</p>
                    <ul>
                    { this.state.reminders.map((rem, idx) => (
                      <li>{rem.tlabel}</li>
                    ))}
                    </ul>
                  </Alert>
              </Col>
            </Row>
            )}

            <Row>
              <Col className="small-padding" xl={3} md={6} sm={12} xs={12}>
                <DisplayWidget
                  title="Monthily revenue"
                  number={this.state.visualizations.revenue}
                  color="secondary"
                />
              </Col>
              <Col className="small-padding" xl={3} md={6} sm={12} xs={12}>
                <DisplayWidget
                  title="Trucks"
                  number={this.state.visualizations.trucks}
                  color="danger"
                />
              </Col>
              <Col className="small-padding" xl={3} md={6} sm={12} xs={12}>
                <DisplayWidget
                  title="Monthily expenses"
                  number={this.state.visualizations.expenses}
                  color="primary"
                />
              </Col>
              <Col className="small-padding" xl={3} md={6} sm={12} xs={12}>
                <DisplayWidget
                  title="Trips"
                  number={this.state.visualizations.trips}
                  color="success"
                />
              </Col>
            </Row>
            <br></br>
            <Row>
            <Col xl={6} lg={12} md={12}>
                <Card>
                  <CardHeader className="text-secondary">Revenue</CardHeader>
                  <CardBody>
                    <Line data={this.genRevenueLineData(this.state.revexp, { type: 'line', fill: false })} />
                  </CardBody>
                </Card>
              </Col>
              <Col xl={6} lg={12} md={12}>
                <Card>
                  <CardHeader className="text-primary">Truck revenue</CardHeader>
                  <CardBody>
                    <HorizontalBar data={this.genTruckRevHoriBarData(this.state.toptentrucks).data} options={this.genTruckRevHoriBarData(this.state.toptentrucks).options} />
                  </CardBody>
                </Card>
              </Col>
              <Col xl={6} lg={12} md={12}>
                <Card>
                  <CardHeader className="text-primary">Driver revenue</CardHeader>
                  <CardBody>
                    <Bar data={this.genDriverRevenueBarData(this.state.driver_rev)}/>
                  </CardBody>
                </Card>
              </Col>
              <Col xl={6} lg={12} md={12}>
                <Card>
                  <CardHeader className="text-primary">Fuel Consumption</CardHeader>
                  <CardBody>
                    <Line data={this.genFuelConsumptionLineData(this.state.fuel_cons, { type: 'line', fill: false })} />
                  </CardBody>
                </Card>
              </Col>
              <Col xl={6} lg={12} md={12}>
                <Card>
                  <CardHeader className="text-primary">Fuel vs Miles</CardHeader>
                  <CardBody>
                    <Scatter data={this.genFuelMilesScatterData(this.state.fuel_mile.f, this.state.fuel_mile.m)} />
                  </CardBody>
                </Card>
              </Col>
              <Col xl={6} lg={12} md={12}>
                <Card>
                  <CardHeader className="text-primary">Fuel vs Revenue</CardHeader>
                  <CardBody>
                    <Scatter data={this.genFuelRevenueScatterData(this.state.fuel_rev.f, this.state.fuel_rev.r)} />
                  </CardBody>
                </Card>
              </Col>
              <Col xl={6} lg={12} md={12}>
                <Card>
                  <CardHeader className="text-primary">Fuel vs Weight</CardHeader>
                  <CardBody>
                    <Scatter data={this.genFuelWeightScatterData(this.state.fuel_weight.f, this.state.fuel_weight.w)} />
                  </CardBody>
                </Card>
              </Col>
              <Col xl={6} lg={12} md={12}>
                <Card>
                  <CardHeader className="text-secondary">Count of Loads by Broker</CardHeader>
                  <CardBody>
                    <HorizontalBar data={this.genBrokerLoadsCountHoriBarData(this.state.broker_loads).data} options={this.genBrokerLoadsCountHoriBarData(this.state.broker_loads).options} />
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Page>
          )}
      </Consumer>
    );
  }
}
export default DashboardPage;
